<template>
  <div class="left fl">
    <ul>
      <router-link to="/center"><li class="wallet" :class="{active:key == '/center'}"><i class="qb"></i><span style="color: #333;vertical-align: middle">钱包</span></li> </router-link>
      <router-link to="/transfer" ><li class="account" :class="{active:key == '/transfer'}"><i class="zz"></i><span style="color: #333;vertical-align: middle">内部转账记录</span></li></router-link>
      <router-link to="/withdraw"><li class="cash" :class="{active:key == '/withdraw'}"><i class="tx"></i><span style="color: #333;vertical-align: middle">提现记录</span></li></router-link>
      <router-link to="/wechat"><li class="qrCode" :class="{active:key == '/wechat'}"><i class="sc"></i><span style="color: #333;vertical-align: middle">上传微信二维码</span></li></router-link>
      <router-link to="/invite"><li class="invite" :class="{active:key == '/invite'}"><i class="yq"></i><span style="color: #333;vertical-align: middle">邀请好友</span></li></router-link>
      <router-link to="/backup"><li class="backup" :class="{active:key == '/backup'}"><i class="bf"></i><span style="color: #333;vertical-align: middle">备份账号</span></li></router-link>
      <router-link to="/notice"><li class="note" :class="{active:key == '/notice'}"><i class="gg"></i><span style="color: #333;vertical-align: middle">公告</span></li></router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Layout',
  computed: {
      key()
      {
         return this.$route.path
      }
  },
  methods: {
  }
}
</script>

