  .content .center-box .left ul li{height: 58px;line-height: 58px;cursor: pointer;border-left: 2px solid #fff;}
  .content .center-box .left ul li a{font-size:16px;color:rgba(26,26,26,1);vertical-align: middle;}
  .content .center-box .left ul .active{background: #F2F8FF;border-left: 2px solid #0076FF;}
  .content .center-box .left ul .active a{display: inline-block;color: #0076FF;}
  .content .center-box .left ul .active span{color: #0076FF!important;}
  .content .center-box .left ul .wallet i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/wallet.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .account i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/zhuanzhang_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .cash i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/tixian_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .qrCode i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/shangchuan_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .note i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/gonggao_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .invite i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/yaoqing_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .backup i{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/password_normal.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}
  .content .center-box .left ul .active .qb{display: inline-block;width: 18px;height: 16px;background: url(../static/images/wallet-active.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .zz{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/zhuanzhang_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .tx{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/tixian_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .sc{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/shangchuan_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .yq{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/yaoqing_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .bf{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/password_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

  .content .center-box .left ul .active .gg{display: inline-block;width: 18px;height: 16px;background: url(../static/images/index/gonggao_hover.png) no-repeat center;background-size: 18px 16px;vertical-align: middle;margin-right: 10px;margin-left: 35px;}

</style>



