<template>
  <div class="header">
    <div class="nav warp-1200">
      <router-link to="/" class="logo">官方财富树</router-link>
      <ul>
        <li class="head_about" :class="{cur:key == '/'}"><router-link to="/">首页</router-link></li>
        <li class="head_about" :class="{cur:key == '/rule'}"><router-link to="/rule">规则与详解</router-link></li>
      </ul>
      <div class="login-box">
        <template v-if="!name">
          <router-link  to="/login" :class="{active:key=='/login'}">登录</router-link><span>|</span><router-link to="/register" :class="{active:key=='/register'}">注册</router-link>
        </template>
      <template  v-else>
        <router-link to="/center">{{name}}</router-link><span>|</span><a href="javascript:void (0)" @click="logout">退出</a>
      </template>
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
export default {
  name: 'Layout',
  computed: {
      ...mapGetters([
          'avatar',
          'name',
          'token'
      ]),
      key()
      {
         return this.$route.path
      }
  },
  methods: {
      // 退出登录
      logout()
      {
          this.$store.dispatch('user/logout').then(() => {

              if (this.key == '/center')
              {
                  this.$router.push('/')
              }

          }).catch(() => {

          })
      },
  }
}
</script>

<style scoped>
  .header{background:linear-gradient(90deg,rgba(102,95,238,1),rgba(39,194,222,1));}
  .header .nav{height: 70px;overflow: hidden;}
  .header .nav .logo {display: block;float: left;line-height: 50px;margin: 10px 0;color: rgba(255,255,255,1);padding-left: 141px;text-indent: -9999px;background: url(../static/images/logos.png) no-repeat left top/141px 50px;}
  .header .nav ul{float: left;overflow: hidden;margin-left: 550px;}
  .header .nav ul li{float: left;line-height: 69px;}
  .header .nav ul li a{display: block;font-size: 14px;color: #fff;padding: 0 30px;}
  .header .nav ul .cur{border-bottom: 1px solid  #EAC133;}
  .header .nav ul .cur a{color: #EAC133;}
  .header .nav .login-box{float: right;line-height: 70px;}
  .header .nav .login-box a{font-size: 14px;color: #fff;padding: 3px 14px;margin: 0 10px;}
  .header .nav .login-box a:hover{background: #EAC133;border-radius:12px;}
  .header .nav .login-box a:last-child{margin-right: 0;}
  .header .nav .login-box span{color: #EAC133;font-size: 14px;}
</style>



