import request from '@/utils/request'

export function wallet(data) {
    return request({
        url: '/user/wallet',
        method: 'get',
        data
    })
}

export function upwechat(data) {
    return request({
        url: '/user/wechat',
        method: 'post',
        data
    })
}

export function withdrawInfo(data) {
    return request({
        url: '/user/withdrawInfo',
        method: 'get',
        data
    })
}

export function withdraw(data) {
    return request({
        url: '/user/withdraw',
        method: 'post',
        data
    })
}

export function withdrawList(params) {
    return request({
        url: '/user/withdrawList',
        method: 'get',
        params
    })
}

export function transfer(data) {
    return request({
        url: '/user/transfer',
        method: 'post',
        data
    })
}
export function transferList(params) {
    return request({
        url: '/user/transferList',
        method: 'get',
        params
    })
}

export function invite() {
    return request({
        url: '/user/inviteInfo',
        method: 'get',

    })
}

export function backup() {
    return request({
        url: '/user/backupInfo',
        method: 'get',

    })
}











